/* GLOBAL */

@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap");
:root {
  --main-dark: rgb(35, 35, 43);;
  --main-light: white;
  --main-theme: #eb4034;
}
* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Source Sans Pro", sans-serif;
  overflow-x: hidden;
}
#social-media li {
  margin: 1em;
}
@media (max-width: 320px) {
  #social-media-title {
    font-size: 1.5em;
  }
}

/* LIGHT  */

.LightApp header {
  display: flex;
  justify-content: center;
  height: 8vh;
  background-color: var(--main-light);
}

/* DARK  */

.DarkApp {
  background-color: var(--main-dark);
  padding-bottom: 0.001%;
}
.DarkApp header {
  display: flex;
  justify-content: center;
  height: 8vh;
  background-color: var(--main-dark);
}