/* NEUTRAL  */
.Footer {
   height: 7em;
   border-top: 3px solid var(--main-theme);
   display: flex;
   align-items: center;
   flex-direction: row;
}
@media (max-width: 500px){
   .Footer {
      height: 2em;
   }
   .logoimg {
      display: none;
   }
}
.Go-to-top {
   margin-right: 1em;
   margin-left: auto;
   text-decoration: none;
   font-size: 0.9em;
}
.Go-to-top:hover {
   text-decoration: underline;
}
.logoimg {
   border-radius: 1em;
   margin: 1em 0.5em;
   height: 5em;
   width: 5em;
   box-shadow: 0px 0px 13px -2px #000000;
}
.Footer p { margin: 1vw; }
#identicon { background-color: white; }
.Copy { font-size: 0.9em; }

/* LIGHT  */
.LightFooter {
   background-color: rgb(226, 226, 226);
}
.LightCopy {
   color: #586069;
}
.LightGo-to-top {
   color: #586069;
}


/* DARK  */
.DarkFooter {
   background-color: black;
}
.DarkCopy {
   color: white;
}
.DarkGo-to-top {
   color: white;
}